<template>
    <div>
        <div class="header">
            <!-- 页面头部 -->
            <p> <span></span> 30天无忧退货</p>
            <p> <span></span> 48小时快速退款</p>
            <p> <span></span> 满88元免邮费</p>
        </div>
        <!-- 购物车商品列表 -->
        <van-swipe-cell v-for="item in list" :key="item.id">
            <div class="cart-list">
                <div class="cart-item">
                    <van-checkbox v-model="item.isChecked"></van-checkbox>
                    <img :src="item.list_pic_url" alt="" @click="goInfo(item.goods_id)">
                    <p @click="goInfo(item.goods_id)">{{item.goods_name}}
                        <span>￥{{item.retail_price}}</span>
                    </p>
                    <h2>x{{item.number}}</h2>
                </div>
            </div>
            <template #right>
                <!-- 删除 -->
                <van-button @click="delShop(item.id)" square type="danger" text="删除" style="height: 100%;" />
            </template>

        </van-swipe-cell>
        <!-- 提交订单 -->
        <van-submit-bar :price="price" @submit="shop" button-text="提交订单" style="bottom: 50px">
            <!-- 全选 -->
            <van-checkbox v-model="allChecked">{{listStr}}全选</van-checkbox>
        </van-submit-bar>

    </div>
</template>

<script>
    // 获取接口
    import {
        cartList,
        deleteAction,
        submitAction

    } from '@/api/cart/index.js';
    import {
        Toast,
        Dialog,
    } from 'vant';

    export default {
        data() {
            return {
                // 主要数据
                list: [],

                num: 0,
                listStr:'',

            };
        },
        computed: {
            // 计算总价
            price() {
                return this.list.reduce((total, cur) => {
                    if (cur.isChecked) {
                        return total + cur.retail_price * cur.number * 100
                    } else {
                        return total
                    }
                }, 0)

            },

            allChecked: {
                // 判断全选状态
                set(val) {
                    if (val) {
                        this.list.forEach(ele => ele.isChecked = true)
                    } else {
                        this.list.forEach(ele => ele.isChecked = false)
                    }
                },
                get() {
                    return this.list.every(ele => ele.isChecked)

                }
            }
        },
        created() {

        },
        mounted() {
            this.init();
        },
        methods: {
            init() {
                // 获取购物车商品列表
                cartList({
                    openId: localStorage.getItem('openId'),
                }).then(res => {
                    res.data.data.forEach(ele => {
                        ele.isChecked = false;
                    });
                    this.list = res.data.data
                })
            },
            // 跳转商品详情
            goInfo(id) {
                this.$router.push(`/category/goods/info?id=${id}`)
            },
            // 商品删除
            delShop(id) {
                Dialog.confirm({
                        title: '商品删除',
                        message: '确认删除吗?',
                    })
                    .then(() => {
                        deleteAction({
                            id: id
                        }).then(res => {
                            if (res.data.data) {
                                Toast.success('删除成功');
                            }
                        })
                        // 删除后刷新列表数据
                        this.init()
                    })
                    .catch(() => {});
            },
            // 点击下单按键
            shop() {
                // 判断是否选了商品
                var listed = this.list.filter(ele => ele.isChecked == true)
                    .map(el => el.goods_id).join(',')
                if (this.price == 0) {
                    Dialog.alert({
                        message: '还没有选择商品',
                    }).then(() => {
                        // on close
                    });
                } else {
                    // 创建订单信息
                    submitAction({
                        allPrise: this.price,
                        goodsId: listed,
                        openId: localStorage.getItem('openId')
                    }).then(res => {
                        console.log(res);
                    })
                    // 跳转订单确认页面
                    this.$router.push(`/cart/order?allPrise=${this.price}&goodsId=${listed}&num=${this.num}`)
                }
            }
        },
    };
</script>

<style scoped lang="scss">
    .header {
        padding: 20px 10px;
        display: flex;
        justify-content: space-between;

        p {
            display: flex;
            justify-content: center;
            align-items: center;

            span {
                display: block;
                width: 4px;
                height: 4px;
                border-radius: 50%;
                border: 1px solid #f00;
                margin-right: 4px;
            }

            color: #666;
        }
    }

    .cart-list {
        background-color: #fff;

        .cart-item {
            van-checkbox {
                width: 5%;
            }

            border-bottom: 1px solid #ccc;
            padding: 20px 20px 10px 20px;
            display: flex;
            justify-content: space-between;

            img {
                width: 63px;
                height: 63px;
            }

            p {
                width: 60%;
                float: left;
                margin-top: 15px;

                span {
                    display: block;
                    margin-top: 15px;
                }
            }

            h2 {
                font-size: 14px;
                font-weight: normal;
                padding: 10px;
                line-height: 65px;
            }
        }


    }
</style>